.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.nav-menu{
    display: flex;
}

.nav-link{
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.5rem 1rem;
    text-decoration: none;
    height: 100%;
}

.nav-link:hover{
    border-bottom: 3px solid #fff;
    transition: all 0.2s ease-out;
    position: relative;
    top: -3px;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
}